import React, { useEffect, useRef, useState } from 'react';
import { Space, Table, Modal, Input } from 'antd';
import {
  getPairListService,
  updatePairInfoService,
  deletePairService,
} from './service/index';

function App() {
  const inputRef = useRef(null);
  const [pairList, setPairList] = useState([]);
  const [paginationInfo, setPaginationInfo] = useState({
    current: 1,
    pageSize: 10,
    total: 0,
  });
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [updatePair, setUpdatepair] = useState({});
  const handlePaginationChange = (pagination) => {
    setPaginationInfo({
      ...pagination,
    });
  };
  const handleUpdateTwitter = (target) => {
    setIsModalOpen(true);
    setUpdatepair({
      ...target,
    });
  };
  const handleDeletePair = (target) => {
    deletePairService(target.address);
    getPairList();
  };
  const handleOk = () => {
    setIsModalOpen(false);
    inputRef.current.input.value = ''; // 
    updatePairInfo();
    getPairList();
  };
  const handleCancel = () => {
    setIsModalOpen(false);
  };
  async function updatePairInfo() {
    const res = await updatePairInfoService(updatePair);
    console.log('res', res);
  }
  async function getPairList() {
    const { current, pageSize } = paginationInfo;
    const res = await getPairListService(current, pageSize);
    const {
      success,
      data: { list, totalCount },
    } = res;
    if (success) {
      setPairList([...list]);
      setPaginationInfo({
        ...paginationInfo,
        total: totalCount,
      });
    } else {
      alert('');
    }
  }
  useEffect(() => {
    getPairList();
  }, [paginationInfo.current, paginationInfo.pageSize, paginationInfo.total]);
  const columns = [
    {
      title: '',
      dataIndex: 'ticker',
      key: 'ticker',
    },
    {
      title: '',
      dataIndex: 'address',
      key: 'address',
    },
    {
      title: '',
      dataIndex: 'creationTime',
      key: 'creationTime',
    },
    {
      title: '',
      dataIndex: 'twitterAccount',
      key: 'twitterAccount',
    },
    {
      title: '',
      key: 'action',
      render: (_, record) => (
        <Space size='middle'>
          <a
            onClick={() => {
              handleUpdateTwitter(_);
            }}
          >
            
          </a>
          <a
            onClick={() => {
              handleDeletePair(_);
            }}
          >
            
          </a>
        </Space>
      ),
    },
  ];
  return (
    <div className='App'>
      <Table
        columns={columns}
        dataSource={pairList}
        pagination={paginationInfo}
        onChange={(pagination) => {
          handlePaginationChange(pagination);
        }}
      />
      <Modal
        title=''
        open={isModalOpen}
        onOk={handleOk}
        onCancel={handleCancel}
        okText=''
        cancelText=''
      >
        <Input
          ref={inputRef}
          placeholder=''
          onChange={(e) => {
            setUpdatepair({
              ...updatePair,
              twitterAccount: e.target.value,
            });
          }}
        />
      </Modal>
    </div>
  );
}

export default App;
